@import '../../theme.scss';
.calibration {
    height: 100vh;
    width: 100vw !important;
    position: relative;
    top: 0;
    left: 0;
    flex-direction: row !important;
    display: flex;
    justify-content: center !important;
    align-items: center !important;
    .left {
        margin: 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        border: 1px solid $primaryColorLight;
        padding: 3em;
        box-sizing: border-box;
        border-radius: 4px;
        .load-cell {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            align-items: center;
            position: relative;
            border-bottom: 4px solid $primaryColor;
            width: 240px;
            height: 180px;
            .weight {
                width: 120px;
                height: 30px;
                background: $primaryColorLight;
                margin: 2px 0 0 0;
                border-radius: 4px;
            }
            &::after {
                content: '';
                height: 20px;
                width: 10px;
                background: $primaryColor;
                position: absolute;
                top: 100%;
                left: 50%;
            }
        }
        .label {
            font-size: 2em;
            text-align: center;
        }
        .arrow {
            position: absolute;
            top: 168px;
            left: 50%;
            transform: translate(-50%);
            max-height: 36px;
            transition: ease 0.3s all;
            opacity: 0;
        }
        .arrow.active {
            opacity: 1;
        }
    }
    .center {
        width: 50%;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .console {
            display: flex;
            justify-content: center;
            align-items: center;
            .cpu {
                img {
                    max-width: 100%;
                }
                flex: 1;
            }
            .wireContainer {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                flex: 2;
                .wire {
                    height: 1px;
                    width: 100px;
                    background: $primaryColor;
                }
            }
        }
        .motor.active {
            filter: grayscale(0);
        }
        .label {
            font-size: 2em;
            text-align: center;
        }
    }
    .right {
        position: relative;
        .chart {
            width: 420px;
            height: 250px;
            border: 1px solid $primaryColorLight;
            padding: 2em 2em 2em 0;
            border-radius: 4px;

            .label {
                position: absolute;
                top: 320px;
                left: 50%;
                transform: translate(-50%);
                font-size: 2em;
                transition: ease 0.3s all;
            }
        }

        .label {
            position: absolute;
            top: 64px;
            left: 45%;
            transform: translate(-50%);
            font-size: 2em;
            transition: ease 0.3s all;
        }
    }
    .axis {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        &::after {
            content: '';
            border-top: 4px solid $secondaryColor;
            height: 0;
            width: 100%;
            transition: ease 0.3s all;
            filter: grayscale(1);
        }
    }
}
